<template>
  <div style="font-size:0.7rem;">
    <div
      id="plice_top"
      class="c_ff"
    >
      <p
        @click="getRoom"
        class="room_choose"
      >
        {{room}}
        <img
          class="img_bottom"
          src="../../assets/five/bottom.png"
          alt
        />
      </p>&emsp;&ensp;
      <p
        @click="getTime"
        class="time_choose"
      >
        <van-cell :value="date" />
        <img
          class="img_bottom"
          src="../../assets/five/bottom.png"
          alt
        />
      </p>
    </div>
    <div id="search_field">
      <form class="search_form">
        <img
          src="../../assets/five/search.png"
          alt
        />
        <input
          onkeydown="if(event.keyCode==13){event.keyCode=0;event.returnValue=false}"
          type="search"
          v-model="servalue"
          placeholder="姓名/身份证查询"
        />
      </form>
    </div>
    <div
      v-if="info.length == 0"
      style="margin-top:20px;font-size:16px;"
    >暂无数据</div>
    <table
      v-else
      border-spacing="0"
      border-collapse="collapse"
      cellspacing="0"
      cellpadding="0"
      id="plice_table"
    >
      <thead>
        <tr>
          <th
            :style="{'width':computerWidth}"
            class="center"
          >房间号</th>
          <th
            :style="{'width':computerWidth}"
            class="center"
          >姓名</th>
          <th
            :style="{'width':computerWidth}"
            class="center"
          >身份证号</th>
          <th
            :style="{'width':computerWidth}"
            class="center"
          >民族</th>
          <th
            :style="{'width':computerWidth}"
            class="center"
          >入住</th>
          <th
            :style="{'width':computerWidth}"
            class="center"
          >退房</th>
          <th
            :style="{'width':computerWidth}"
            class="center"
          >来源</th>
        </tr>
      </thead>
      <tbody style="width: 500px;">
        <tr
          v-for="(item,i) in info"
          :key="i"
          :class="Number(i)%2!=1 ? 'bg2': 'bg1'"
        >
          <td :style="{'width':computerWidth}">{{ item.roomSn }}</td>
          <td :style="{'width':computerWidth}">
            {{ item.realName?item.realName:'' }}
          </td>
          <td
            :style="{'width':computerWidth}"
            class="left"
          >
            {{ item.idenIds?item.idenIds:'' }}
          </td>
          <td :style="{'width':computerWidth}">{{ item.race }}</td>
          <td
            :style="{'width':computerWidth}"
            class="left"
          >
            {{ item.roomTime?item.roomTime.substr(0,10):'' }}
            <br />
            {{ item.roomTime?item.roomTime.substr(11,8):'' }}
          </td>
          <td
            :style="{'width':computerWidth}"
            class="left"
            v-if="item.leaveTime"
          >
            {{ item.leaveTime?item.leaveTime.substr(0,10):'' }}
            <br />
            {{ item.leaveTime?item.leaveTime.substr(11,8):'' }}
          </td>
          <td
            :style="{'width':computerWidth}"
            v-else
          >在住</td>
          <td :style="{'width':computerWidth}">{{ orderSourceMap[item.orderSource] }}</td>
        </tr>
      </tbody>
    </table>
    <img
      src="@/assets/police/export.png"
      class="export"
      @click="show_export = true,
                dataForm.startTime = null;
                dataForm.endTime = null;
                dataForm.email = null;
                dataTime = []"
    >
    <!-- 全部房间 -->
    <mt-popup
      v-model="show_1"
      position="bottom"
      class="roomShow"
    >
      <div
        class="type_box"
        @click="choosed_statu('全部房间')"
      >全部房间</div>
      <div
        v-for="(item,i) in roomStatus"
        :key="i"
        @click="choosed_statu(item)"
        class="type_box"
      >
        <span>{{item.roomSn}}</span>
      </div>
    </mt-popup>
    <van-dialog
      title="请选择要导出的时间段"
      v-model="show_export"
      show-cancel-button
      :before-close="getUserInfo"
    >
      <div
        class="live_type_box"
        v-if="radio"
      >
        <div class="live_type">入住类型</div>
        <van-radio-group
          v-model="radio"
          class="aaa"
        >
          <van-radio name="1">当前入住</van-radio>
          <van-radio name="0">历史入住</van-radio>
        </van-radio-group>
      </div>
      <van-field
        v-if="radio == '0'"
        label="选择时间"
        v-model="dataTime"
        placeholder="请选择时间段"
        label-class='ml30'
        readonly
        @click="openTime('1')"
      />
      <van-field
        label="邮箱账号"
        v-model="dataForm.email"
        label-class='ml30'
        placeholder="请输入邮箱账号"
        type="email"
      />
    </van-dialog>
    <!-- 选择开始、结束时间 -->
    <van-calendar
      v-model="time_show"
      type="range"
      :min-date="minDate"
      :max-date="maxDate"
      @confirm="onConfirm"
    />
  </div>
</template>
<script>
import { GetBusinessRoombyhotelHotelId, GetBusinessOrderroomuserQuery, PostBusinessOrderroomuserExport } from '@/api/api_business_hotel_and_scale.js';
import { GetSystemDictDataByType } from '@/api/api_query';
import { Toast } from 'mint-ui';
import axios from 'axios';
export default {
    data() {
        return {
            info: [],
            value: '',
            room: '全部房间',
            roomStatus: [],
            show_1: false,
            orderSourceMap: [],
            year: '',
            time_show: false,
            isClicked: '',
            minDate: new Date(), //设置过期时间为当天
            show: false,
            show_export: false,
            show_time: false,
            date: '选择日期区间',
            defaultDate: ['2019-07-02', '2019-07-05'],
            www: '',
            servalue: '',
            maxDate: '',
            dataForm: {
                startTime: null,
                endTime: null,
                hotelId: this.$route.query.hotelId,
                status: null,
                email: null
            },
            doble: [],
            dataTime: null,
            radio: '1'
        };
    },
    created() {
        this.$store.commit('setHeader', '入住人登记信息');
        this.hotelId = this.$route.query.hotelId;
        this.data = { hotelId: this.hotelId, roomId: '0' };
        var dayjs = require('dayjs');
        this.cu_time = dayjs().subtract(2, 'month');
        this.minDate = new Date(this.cu_time);
        this.maxDate = new Date(dayjs());
        this.getInfo();
        this.getSource();
    },
    computed: {
        computerWidth() {
            return window.innerWidth / 7 + 'px';
        },
        computerHeight() {
            return 100 + 'vh' - '250px';
        }
    },
    methods: {
        // 当前时间
        getNowFormatDate() {
            var date = new Date();
            var seperator1 = '-';
            var year = date.getFullYear();
            var month = date.getMonth() + 1;
            var strDate = date.getDate();
            if (month >= 1 && month <= 9) {
                month = '0' + month;
            }
            if (strDate >= 0 && strDate <= 9) {
                strDate = '0' + strDate;
            }
            var currentdate = year + seperator1 + month + seperator1 + strDate;
            return currentdate;
        },
        getTime() {
            this.time_show = true;
            this.typeNum = null;
            this.doble = [];
        },
        formatDate(date) {
            if (!this.typeNum) {
                let doble = `${date.getFullYear()}-${date.getMonth() + 1 < 10 ? '0' + (date.getMonth() + 1) : date.getMonth() + 1}-${date.getDate() < 10 ? '0' + date.getDate() : date.getDate()}`;
                this.doble.push(doble);
                this.data.startTime = this.doble[0];
                this.data.endTime = this.doble[1];
                this.getInfo();
                return `${date.getMonth() + 1 < 10 ? '0' + (date.getMonth() + 1) : date.getMonth() + 1}/${date.getDate() < 10 ? '0' + date.getDate() : date.getDate()}`;
            } else {
                let doble = `${date.getFullYear()}-${date.getMonth() + 1 < 10 ? '0' + (date.getMonth() + 1) : date.getMonth() + 1}-${date.getDate() < 10 ? '0' + date.getDate() : date.getDate()}`;
                this.doble.push(doble);
                this.dataForm.startTime = this.doble[0];
                this.dataForm.endTime = this.doble[1];
                this.dataTime = this.doble[0] + '至' + this.doble[1];
                this.show_export = true;
            }
        },
        onConfirm(date) {
            const [start, end] = date;
            this.time_show = false;
            if (!this.typeNum) {
                this.date = `${this.formatDate(start)} - ${this.formatDate(end)}`;
            } else {
                this.formatDate(start);
                this.formatDate(end);
            }
        },
        // 根据条件查询酒店入住人信息
        getInfo() {
            let data = this.data;
            GetBusinessOrderroomuserQuery(data).done((res) => {
                this.info = res.data;
            });
        },
        // 订单来源
        getSource() {
            GetSystemDictDataByType({ dictType: 'business_order_source' }).done((res) => {
                let orderSourceMap = {};
                res.data.forEach((item) => {
                    orderSourceMap[item.dictSort] = item.dictLabel;
                });
                this.orderSourceMap = orderSourceMap;
            });
        },
        getRoom() {
            this.getRoomInfo();
            this.show_1 = true;
        },
        getRoomInfo() {
            // 全部房间
            GetBusinessRoombyhotelHotelId(this.hotelId).done((res) => {
                this.roomStatus = res.data;
            });
        },
        // 选中(全部房间)
        choosed_statu(item) {
            this.servalue = '';
            if (item != '全部房间') {
                this.room = item.roomSn;
                this.data.roomId = item.id;
            } else {
                this.room = item;
                delete this.data.roomUserInfo;
                this.data.roomId = '0';
            }
            this.show_1 = false;
            this.getInfo();
        },
        // 展开开始时间、结束时间
        openTime(type) {
            this.doble = [];
            this.typeNum = type;
            this.show_export = false;
            this.time_show = true;
        },
        // 选择时间
        onConfirmTime(e) {
            this.time_show = false;
            this.show_export = true;
        },
        // 导出
        getUserInfo(action, done) {
            if (action == 'confirm') {
                done(false);
                this.dataForm.status = this.radio;
                this.show_export = false;
                if (!this.dataForm.email) {
                    axios({
                        method: 'post',
                        url: '/api/business/orderroomuser/export',
                        params: this.dataForm,
                        responseType: 'arraybuffer' //注意这个参数必须加，不然会出现下载好的文件打开是乱码情况
                    }).then((res) => {
                        const data = res.data;
                        const url = window.URL.createObjectURL(new Blob([data], { type: 'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet' }));
                        const link = document.createElement('a');
                        link.style.display = 'none';
                        link.href = url;
                        link.setAttribute('download', '入住人登记信息');
                        //注意：上面的self.$route.query.type是你要下载的文件名加文件后缀，例如test.docx
                        document.body.appendChild(link);
                        link.click();
                        document.body.removeChild(link);
                    });
                } else {
                    PostBusinessOrderroomuserExport(this.dataForm).done((res) => {
                        Toast(res.msg);
                    });
                }
            } else if (action == 'cancel') {
                done(false);
                this.dataForm.startTime = null;
                this.dataForm.endTime = null;
                this.dataTime = [];
                this.dataForm.email = null;
                this.show_export = false;
            }
        }
    },
    watch: {
        servalue: function () {
            this.data.roomId = '0';
            this.room = '全部房间';
            this.data.roomUserInfo = this.servalue;
            this.getInfo();
        }
    }
};
</script>
<style scoped>
.c_ff {
    color: #000;
    font-weight: bold;
    background: #fff;
}
.van-cell {
    padding: 0;
    line-height: 40px;
    padding-right: 4px;
}
.time_choose {
    display: flex;
    align-items: baseline;
}
#search_field {
    background: #fff;
    padding-bottom: 10px;
}
#search_field > .search_form {
    display: flex;
}
#search_field > .search_form > img {
    width: 18px;
    height: 18px;
    position: absolute;
    top: 96px;
    left: 30px;
}
#search_field > .search_form > input {
    margin: 0 auto;
    background: #f6f6f6;
    width: 90%;
    border-radius: 30px;
    line-height: 30px;
    text-indent: 3em;
}
.mint-search {
    height: 52px;
}
/deep/ .mint-searchbar {
    background: #fff;
}
/deep/ .mint-searchbar-inner {
    background: #efeff4;
    color: #c4c4c8;
}
/deep/ .mint-searchbar-core {
    background: #efeff4;
    text-indent: 1em;
}
#plice_table tbody tr:nth-child(odd) {
    background: #fff;
}
#plice_table tbody tr:nth-child(even) {
    background: #f8f8f8;
}
#plice_top {
    display: flex;
    justify-content: center;
    line-height: 40px;
}
.img_bottom {
    padding-left: 4px;
    width: 12px;
    height: 12px;
}
#plice_table {
    padding-top: 10px;
    font-size: 0.6rem;
    background: #fff;
    table-layout: fixed;
    word-break: break-all;
    width: 500px;
}
#plice_table thead {
    color: #000;
    border-top: 2px solid #393939;
    line-height: 40px;
    display: block;
    text-align: center;
}
#plice_table tbody tr td {
    border: 1px solid #ebebeb;
    border-width: 0px 1px 1px 0px;
    box-sizing: border-box;
    padding: 2px;
}
.left {
    text-align: left;
}
.center {
    border: solid #ebebeb;
    border-width: 0px 1px 1px 0px;
    box-sizing: border-box;
    text-align: center;
}
table tbody {
    overflow-y: scroll;
    position: fixed;
    height: calc(100% - 190px);
}
.roomShow {
    width: 100%;
    height: 300px;
    position: fixed;
    bottom: 0;
    left: 50%;
    overflow-y: scroll;
}
.type_box {
    border-bottom: 1px solid #cecece;
    line-height: 35px;
    font-size: 0.8rem;
    color: #000;
}
#timebox {
    display: flex;
    justify-content: space-around;
    line-height: 50px;
}
#timebox > p {
    width: 50%;
    border: 1px solid #cecece;
}
.export {
    position: fixed;
    bottom: 10%;
    right: 10%;
    width: 40px;
}
/deep/ .ml30 {
    margin-left: 30px;
}
/deep/ .van-dialog__header {
    padding: 26px 0;
    color: #000;
}
/deep/ .van-field__label {
    color: #000;
}
.live_type_box {
    display: flex;
    justify-content: space-between;
    padding: 10px 22px 10px 30px;
    font-size: 14px;
    color: #000;
}
.live_type {
    width: 86.8px;
    margin-right: 12px;
    text-align: left;
}
.aaa {
    display: flex;
    width: 170px;
    justify-content: space-between;
}
/deep/ .van-radio__label {
    margin-left: 2px;
}
/deep/ .van-radio__icon--checked .van-icon {
    background-color: #ff7d41;
    border-color: #ff7d41;
}
</style>



